@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto);

* {
    margin: 0;
    padding: 0;
}
* {
    font-family: 'Lato', sans-serif;
}

.aboutme {
    background-color: black;
    color: white;
    min-height: 100vh;
    padding: 10px;
    background-image: url(/myportfolio/static/media/dark-low-poly.8ddcf1a8.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}

.first-content {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.text {
    width: 50vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.text-name {
    font-size: 60px;
    margin-bottom: 50px;
}

.text-text {
    font-size: 20px;
    font-weight: lighter;
    margin-bottom: 100px;
}

.photo img {
    width: 30vw;
    height: 650px;
    border-radius: 10px;
}

.second-title,
.video-video video {
    font-size: 50px;
    margin-bottom: 6vh;
    margin-left: 5vw;
    margin-top: 100px;
}

.video {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.video-text {
    width: 40vw;
    font-size: 20px;
    border-left: 1px solid white;
    height: 50%;
    padding-left: 50px;
}

.text .div-btn {
    letter-spacing: 2px;
    text-transform: uppercase;
}

@media (max-width:768px) {
    .text {
        width: 90vw;
        margin-left: 0px;
    }
    .text-name,
    .second-title {
        font-size: 40px;
    }
    .second-title {
        margin-bottom: 2px;
        margin-left: 10px;
    }
    .first-content {
        display: flex;
        flex-direction: column-reverse;
    }
    .photo img {
        width: 90vw;
        height: 90vw;
        margin: 0px 0px 0px 0px;
    }
    .video {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .video-video video {
        margin-left: 10px;
    }
    .video-text {
        width: 100%;
        border: none;
        margin-left: -80px;
    }
}
.experience1 {
    border-style: solid;
    width: 850px;
    border-color: white;
    background: white;
    /* height: 50vh; */
    color: black;
    border-radius: 20px;
    box-shadow: rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset, rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset, rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset, rgba(0, 0, 0, 0.06) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;
    padding: 2.5%;
    margin-bottom: 10vh;
    display: flex;
    flex-direction: column;
}

.experience1 span {
    color: grey;
}

.div-btn {
    color: white;
    background-color: black;
    border-radius: 8px;
    justify-self: flex-end;
    align-self: flex-start;
    padding: 10px;
}

.div-btn {
    box-shadow: inset 0px 0px 0px white;
    transition: all 0.5s !important;
}

.div-btn:hover {
    box-shadow: inset 0px -80px 0px white;
    color: black;
}

.experience1:hover {
    box-shadow: 2px 2px 2px 2px grey;
    -webkit-transform: translateY(-20px) !important;
            transform: translateY(-20px) !important;
}

@media (max-width:900px) {
    .experience1 {
        width: 90vw;
    }
    .experience1 .h2 {
        font-size: 18px;
    }
    .experience1 h4 {
        font-size: 15px;
    }
}
.main {
    background-image: url(/myportfolio/static/media/dark-low-poly.8ddcf1a8.jpg);
    display: flex;
    flex-direction: column;
    align-items: center;
    background-size: cover;
    min-height: 100vh;
}

.title {
    position: relative;
    color: white;
    font-weight: bolder;
    font-size: 60px;
    text-decoration: underline;
    -webkit-text-decoration-color: aqua;
            text-decoration-color: aqua;
    -webkit-text-decoration-style: solid;
            text-decoration-style: solid;
    text-decoration-thickness: 5px;
    margin: 70px 0px;
}

@media (max-width:850px) {
    .title {
        font-size: 40px;
