.skill {
    background-color: black;
    color: white;
    background-image: url(/myportfolio/static/media/dark-low-poly.8ddcf1a8.jpg);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    min-height: 100vh;
    background-size: cover;
    overflow-x: hidden;
}

.skill-bars {
    padding: 45px 35px;
    width: 800px;
    background: #fff;
    box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    margin-bottom: 20px
}

.skill-bars .bar {
    margin: 20px 0;
}

.skill-bars .bar:first-child {
    margin-top: 0px;
}

.skill-bars .bar .info {
    margin-bottom: 5px;
}

.skill-bars .bar .info span {
    font-weight: 500;
    font-size: 17px;
    padding-left: 7px;
    opacity: 0;
    background: inherit;
    color: black;
    -webkit-animation: showText 0.5s 1s linear forwards;
            animation: showText 0.5s 1s linear forwards;
}

@-webkit-keyframes showText {
    100% {
        opacity: 1;
    }
}

@keyframes showText {
    100% {
        opacity: 1;
    }
}

.skill-bars .bar .progress-line {
    height: 10px;
    width: 100%;
    background: #f0f0f0;
    position: relative;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: left;
            transform-origin: left;
    border-radius: 10px;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.05), 0 1px rgba(255, 255, 255, 0.8);
    -webkit-animation: animate 1s cubic-bezier(1, 0, 0.5, 1) forwards;
            animation: animate 1s cubic-bezier(1, 0, 0.5, 1) forwards;
}

@-webkit-keyframes animate {
    100% {
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
    }
}

@keyframes animate {
    100% {
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
    }
}

.bar .progress-line span {
    height: 100%;
    position: absolute;
    border-radius: 10px;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: left;
            transform-origin: left;
    background: black;
    -webkit-animation: animate 1s 1s cubic-bezier(1, 0, 0.5, 1) forwards;
            animation: animate 1s 1s cubic-bezier(1, 0, 0.5, 1) forwards;
}

.bar .progress-line.html span {
    width: 95%;
}

.bar .progress-line.css span {
    width: 90%;
}

.bar .progress-line.javascript span {
    width: 85%;
}

.bar .progress-line.react span {
    width: 85%;
}

.bar .progress-line.c span {
    width: 90%;
}

.bar .progress-line.dsa span {
    width: 75%;
}

.bar .progress-line.oops span {
    width: 70%;
}

.bar .progress-line.dbms span {
    width: 60%;
}

.bar .progress-line.os span {
    width: 60%;
}

.bar .progress-line.sql span {
    width: 65%;
}

.bar .progress-line.bootstrap span {
    width: 75%;
}

.bar .progress-line.material span {
    width: 70%;
}

.bar .progress-line.node span {
    width: 50%;
}

.bar .progress-line.mongo span {
    width: 40%;
}

.progress-line span::before {
    position: absolute;
    content: "";
    top: -10px;
    right: 0;
    height: 0;
    width: 0;
    border: 7px solid transparent;
    border-bottom-width: 0px;
    border-right-width: 0px;
    border-top-color: #000;
    opacity: 0;
    -webkit-animation: showText2 0.5s 1.5s linear forwards;
            animation: showText2 0.5s 1.5s linear forwards;
}

.progress-line span::after {
    position: absolute;
    top: -28px;
    right: 0;
    font-weight: 500;
    background: #000;
    color: #fff;
    padding: 1px 8px;
    font-size: 12px;
    border-radius: 3px;
    opacity: 0;
    -webkit-animation: showText2 0.5s 1.5s linear forwards;
            animation: showText2 0.5s 1.5s linear forwards;
}

@-webkit-keyframes showText2 {
    100% {
        opacity: 1;
    }
}

@keyframes showText2 {
    100% {
        opacity: 1;
    }
}

.progress-line.html span::after {
    content: "95%";
}

.progress-line.css span::after {
    content: "90%";
}

.progress-line.javascript span::after {
    content: "85%";
}

.progress-line.react span::after {
    content: "85%";
}

.progress-line.c span::after {
    content: "90%";
}

.progress-line.dsa span::after {
    content: "75%";
}

.progress-line.oops span::after {
    content: "70%";
}

.progress-line.dbms span::after {
    content: "60%";
}

.progress-line.os span::after {
    content: "60%";
}

.progress-line.sql span::after {
    content: "65%";
}

.progress-line.bootstrap span::after {
    content: "75%";
}

.progress-line.material span::after {
    content: "70%";
}

.progress-line.node span::after {
    content: "50%";
}

.progress-line.mongo span::after {
    content: "40%";
}

